@import 'src/scss/variables.scss';

.socials-sidebar {
    position: fixed;
    right: 0;
    bottom: 0;
    text-align: center;
    margin: 0px 30px;
    

    li {
        list-style-type: none;
        padding-bottom: 10px;
    
        &:hover{
            transform: translateY(-5px);
            transition: .1s;

            svg{
                color: $primary-color;
            }
        }

        svg{
            font-size: 25px;
            @media only screen and (max-width: 768px){
                font-size: 20px;
            }
        }
    }

    &::after {
        content: "";
        margin: auto !important;
        display: block;
        height: 60px;
        width: 2px;
        background-color: $text-color;
        position: relative;
    }

    @media only screen and (max-width: 768px){
        margin: 0px 20px 0px 0px;

    }
}