@import 'src/scss/_variables.scss';

.header{
    padding-top: 100px;
    background: radial-gradient(circle at center left, rgba(199,197,228,1) 1%, rgb(240, 240, 240) 20%, rgba(222,222,222,0) 97%);
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    
    .names{
        position: relative;
        text-align: center;
        font-size: 150px;
        line-height: 250px;

        &-fname{
            margin-left: -650px;
        }
        &-mname{
            margin-right: -400px;
        }

        @media  (max-width: 900px) {
            font-size: 100px;
            line-height: 180px;
    
            &-fname{
                margin-left: 0px;
            }
    
            &-mname{
                margin-right: 0px;
            }
        }

        @media only screen and (max-width: 768px) {
            font-size: 40px;
            line-height: 80px;
           
        }
    }

    .job__title{
        display: flex;
        align-items: center;
        position: relative;
        top: -450px;
        right: -400px;
        width: 500px;

        &-line{
            width: 30%;
            height: 2px;
            border: 1px solid $text-color;
            margin-right: 20px;

            @media (max-width: 900px){
                width: 20%;
            }

        }

        &-text{
            width: 60%;
            font-size: 30px;
            font-weight: 500;

            @media (max-width: 900px) {
                font-size: 20px;
            }
        }

        @media (max-width: 900px){
            top: 0;
            right: 0px;
            left: 300px;
            
        }

        @media only screen and (max-width: 768px) {
            left: 50px;
        }
    }

    .scroll__btn{
        position: absolute;
        width: 35px;
        height: 35px;
        bottom: 20px;

        &:hover{
            cursor: pointer;
        }

        svg{
            width: 100%;
            height: 100%;
            
            
        }

        @media only screen and (max-width: 768px) {
            bottom: 50px;
        }
    }
    @media (max-width: 900px){
       flex-direction: column; 
    }

    @media only screen and (max-width: 768px) {
        background: radial-gradient(circle at center left, rgba(199,197,228,1) 1%, rgb(240, 240, 240) 50%, rgba(222,222,222,0) 97%);
        padding-top: 250px;
        justify-content: flex-start;
    }
    
}

