//Fonts 
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

@font-face {
    font-family: 'soriasoria';
    src: url('/fonts/soria-font-webfont.woff2') format('woff2'),
         url('/fonts/soria-font-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

$main-font: 'Montserrat', sans-serif;
$secondary-font: 'soriasoria';


// Colors
$primary-color: #c7c5e4;
$bright-purple: #cfc5e4;
$pale-purple: #c5cbe4;
$dark-grey: #707070;
$text-color: #1d1d1d;

//Font weight function
$font-weights: (
  "regular": 400,
  "medium": 600,
  "black": 800,
);

@function weight($weight-name) {
    @return map-get($font-weights, $weight-name);
}