@import 'src/scss/variables.scss';

.work{
    background: radial-gradient(circle at center right, rgba(199,197,228,1) 1%, rgb(240, 240, 240) 40%, rgba(222,222,222,0) 97%);
    text-align: center;

    .projects-filter {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;

        &-item {
          padding: 8px 30px;
          border-radius: 50px;
          background-color: white;
          cursor: pointer;
          transition: all 0.3s ease;
          margin: 10px;
          text-transform: uppercase;


          &:first-child{
              margin-left: 0px;
          }
      
          &:hover {
            background-color: $primary-color;
            color: white;
          }
      
          @media screen and (min-width: 2000px) {
            padding: 5px 15px;
            border-radius: 0.85rem;
          }
        }
      
        .item-active {
          background-color: $primary-color;
          color: white;
        }
      }

      .projects{
        padding: 50px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
      
        .project {
          display: flex;
          margin: 50px 0px;
          transition: all 0.3s ease;
          justify-content: center;
          align-items: center;
          width: auto;

          .project-img{
            width: 500px;
            height: 280px;
            position: relative;;
            
            img{
                width: 100%;
                height: 280px;
                object-fit: cover;

                
            }

            @media screen and (min-width: 2000px) {
              height: 350px;
            }

            .project-hover {
              position: absolute;
              display: flex;
              justify-content: center;
              align-items: center;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              width: 100%;
              height: 280px;
              background-color: #c7c5e47e;
              opacity: 0;
              transition: all 0.3s ease;

              &:hover{
                cursor: pointer;
              }
            
              div {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                background-color: #c7c5e4;
                color: #fff;
                cursor: pointer;
                transition: all 0.3s ease;
            
                
                svg {
                  text-align: center;
                  width: 50%;
                  height: 50%;
                  color: white;
                }
      
              }
              
              a:first-child{
                margin-right: 30px;
              }

              @media (max-width: 900px){
                height: 200px;
                justify-content: flex-start;
                padding-left: 50px;
              }
              @media only screen and (max-width: 768px){
                height: 220px;
                justify-content: center;
                align-items: flex-start;
                padding: 50px 0px 0px 0px;
              }
              
            }

            @media (max-width: 900px){
              width: 800px;

              img{
                height: 200px;
              }
            }

            @media only screen and (max-width: 768px){
              width: 100%;
              height: 220px;

              img{
                height: 220px;
              }
            }
        }
    
        .project-content{
          text-align: left;
          position: relative;
          left: -150px;
          width: 550px;
          height: 150px;
          background-color: white;
          border-radius: 10px;
          padding: 20px;
          box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.20);

          .project-title{
            padding-bottom: 15px;
          }

          @media (max-width: 900px){
            width: 100%;
          }

          @media only screen and (max-width: 768px){
            left: 0px;
            width: 90%;
            top: -100px;
          }
      }

      .project-tags{
            position: absolute;
            display: flex;
            margin-top: 220px;
            margin-left: 350px;

            p{
              background-color: #e8e8e8;
              padding: 5px 15px;
              border-radius: 50px;
              margin-right: 40px;
            }
            @media (max-width: 900px){
              margin-left: 120px;
            }
            @media only screen and (max-width: 768px){
              margin-top: 230px;
              margin-left: 50px;
            }
          }


          &:nth-child(even){
            display: flex;
            flex-direction: row-reverse;

            .project-content{
              left: 150px;
            }
            
            .project-tags{
              margin-left: 0px;
              margin-right: 400px;

              @media (max-width: 900px){
                margin-right: 30px;
              }
            }

            @media (max-width: 900px){
              .project-hover{
                justify-content: flex-end;
                padding-right: 50px;
              }
            }

            @media only screen and (max-width: 768px){
              flex-direction: column;

              .project-content{
                left: 0px;
              }

              .project-tags{
                margin-right: 0px;
                margin-left: 40px;
              }

              .project-hover{
                padding: 50px 0px 0px 0px;
                justify-content: center;
              }
            }
          }
          
          @media (max-width: 900px){
            width: 700px;
          }

          @media only screen and (max-width: 768px){
            flex-direction: column;
            width: 100%;
            margin: 25px 0px;
          }
        }
        
        @media only screen and (max-width: 768px){
          width: 100%;
          padding: 0px;
        }
        
      }
      
      @media (max-width: 900px){
        padding-top: 0px !important ;
      }

      @media only screen and (max-width: 768px){
        background: radial-gradient(circle at center, rgba(199,197,228,1) 1%, rgb(240, 240, 240) 40%, rgba(222,222,222,0) 97%);
      }
      
}