@import 'src/scss/variables.scss';

.contact{
    text-align: center;
   
    a{
        text-decoration: underline;
        color: $primary-color;

        &:hover{
            color: $text-color;
        }
    }
    form {
        
        padding: 50px 0px;
        width: 70%;
        margin: 0 auto;
          
          input{
            font-family: $main-font;
              outline: none;
              width: 100%;
              margin-bottom: 20px;
              border-radius: 10px;
              background-color: transparent;
              border: 1px solid $text-color;
              padding: 15px;
          }

          textarea{
            font-family: $main-font;
              width: 100%;
              height: 150px;
              border-radius: none;
              border: 1px solid $text-color;
              border-radius: 10px;
              background-color: transparent;
              padding: 15px;
              outline: none;
          }

          button{
              font-size: 16px;
              width: 100%;
              height: 50px;
            margin-top: 20px; 
            padding: 10px 20px; 
            background-color: $primary-color;
            border: 1px solid $primary-color;
            border-radius: 10px;
            color: white;
            text-transform: uppercase;

            &:hover{
                cursor: pointer;
                background-color: $bright-purple;
                color: white;
            }
          }

          @media (max-width: 900px){
            width: 90%;
          }

          @media only screen and (max-width: 768px){
            width: 100%;
          }
    }
}