@import 'src/scss/_variables.scss';

.about{
    padding: 100px 0px;
    height: 100vh;
    justify-content: space-between !important;
    align-items: center;
    display: flex;
    background: radial-gradient(circle at center right, rgba(199,197,228,1) 1%, rgb(240, 240, 240) 25%, rgba(222,222,222,0) 97%);


    &-image{
        width: 400px;
        
        img{
            width: 100%;
        }

        @media (max-width: 900px){
            padding-right: 50px;
        }

        @media only screen and (max-width: 768px) {
            width: 300px;
            padding-right: 0px;
        }

    }

    &-content{
        width: 55%;  

        .skill-badges{
            width: 70%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;


            .skill{
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 15px;
                width: 80px;
                height: 80px;
                border-radius: 50%;
                background-color: $primary-color;

                svg{
                    width: 50%;
                    height: 80%;
                }

                &:nth-child(1), &:nth-child(5){
                    margin-left: 0;

                    @media only screen and (max-width: 768px) {
                        margin-left: 15px;

                        
                    }
                }

                @media only screen and (max-width: 768px) {
                    &:first-child, &:nth-child(4), &:nth-child(7){
                        margin-left: 0px;
                    }
                }

                @media (max-width: 900px){
                    width: 60px;
                    height: 60px;
                    // margin: 0px;
                }
            }

            @media (max-width: 900px){
                width: 100%;
            }

        }
        @media (max-width: 900px){
            width: 50%;
        }

        @media only screen and (max-width: 768px) {
            width: 100%;
            padding-top: 50px;
            text-align: center;

            h3{
                font-size: 18px;
            }
            h4{
                font-size: 22px;
            }

            p{
                font-size: 15px;
                line-height: 25px;
                // text-align: justify;
            }
        }
    }

    &-desc{
        font-size: 18px;
        line-height: 35px;
        font-weight: weight(regular);
        width: 85%;
        padding-bottom: 20px;

        @media (max-width: 900px){
           width: 100%;
        }
    }

    @media only screen and (max-width: 768px) {
        flex-direction: column;
        align-content: center;
        height: auto;
        background: radial-gradient(circle at center , rgba(199,197,228,1) 1%, rgb(240, 240, 240) 40%, rgba(222,222,222,0) 97%);
    }
}