@import 'src/scss/_variables.scss';

.navbar{
    padding: 20px 50px;  
    position: fixed;
    z-index: 1000;
    width: 100%;
    background-color: rgb(240, 240, 240, 0.25);
    backdrop-filter: blur(4px);

    nav{
        display: flex;
        align-items: center;
        justify-content: space-between;

        // Navigation Logo
        .navbar-logo {
            width: 60px;
            height: 60px;
        
            img{
                height: 100%;
        
                @media  screen and (min-width: 2000px) {
                    height: 100px;
                }
            }
        }

        .navbar-links{
            display: flex;
            justify-content: flex-end;
            align-items: center;
            //takes up the rest of the space that the logo doesnt take
            flex: 1;
        
        li{
            padding: 0px 40px;
            text-transform: uppercase;
            
                div{
                    width: calc(100% + 10px);
                    height: 8px;
                    background-color: transparent;
                    border: none;
                    margin: -10px -5px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
        
                a:hover{
                    color: $text-color;
                }
        
                &:hover{
                    
                    div{
                        background-color: $primary-color;
                        transition: all 0.3s ease-in-out;
                    }
                }
        }
        
        @media screen and (max-width: 900px) {
            display: none;
        }
        }

    }

    
}

// Moblile Navigation
.app__navbar-menu{
    width: 35px;
    height: 35px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    svg{
        width: 70%;
        height: 70%;
    }

    div{
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;
        padding: 50px;
        width: 50%;
        height: 100vh;
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        align-items: flex-end;
        background-color: rgba(197, 203, 228, 0.25);
        backdrop-filter: blur(4px);

        svg{
            width: 35px;
            height: 35px;
            margin: 20px 0px;
        }

        ul{
            width: 100%;
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: flex-end;
            flex-direction: column;
            align-items: flex-end;
            justify-content: flex-end;

            li{
                text-transform: uppercase;
                padding-bottom: 30px;
    
                a:hover{
                    color: $primary-color;
                    transition: all 0.3s ease-in-out;
                }
            }
        }  
        
        @media screen and (min-width: 900px ) {
            display: block;
        }
    }

    @media screen and (min-width: 900px ) {
        display: none;
    }
}